<template>
  <v-layout>
    <br>
    <b-jumbotron id="jumbotron" style="background: url('https://cdn.stocksnap.io/img-thumbs/960w/49FQQBTLIN.jpg');">
      <template slot="header">
        星云DAPP雷达
      </template>
      <template slot="lead">
        我们聚合了大量优秀的基于星云链的DAPP
      </template>
      <hr class="my-4">
      <p @click="pushNew">
        你也可以上传自己的星云DAPP，和大家一起分享：）
      </p>


      <p>本ÐApp需要用谷歌浏览器打开，并安装 <a href="https://github.com/ChengOrangeJu/WebExtensionWallet">星云钱包插件</a></p>
    </b-jumbotron>
    <hr>
    <!--<ul class="todo-list">-->
      <!--<li v-for = "todo in todos">-->
        <!--<div v-show = "todo.edit == false">-->
          <!--<label @dblclick = "todo.edit = true"> {{todo.title}} </label>-->
        <!--</div>-->
        <!--<input v-show = "todo.edit == true" v-model = "todo.title"-->
               <!--v-on:blur= "todo.edit=false; $emit('update')"-->
               <!--@keyup.enter = "todo.edit=false; $emit('update')">-->
      <!--</li>-->
    <!--</ul>-->
    <b-container  class="">
      <b-row class="row" v-for="(item, index) in list" :key="index"  @click="onPush(item)">

        <b-col cols="4" class="text-center">
          <img class="" :src='item.imgUrl'  width="100%" style="margin-bottom: 20px">


        </b-col>
        <b-col>
          <h5 class="mt-0 mb-1">{{item.title}}</h5>
          <p>{{item.desc}}</p>
        </b-col>

      </b-row>

    </b-container>
    <br>
    <br>
  </v-layout>
</template>
<script>

  import NebPay from 'nebpay.js';
  import nebulas from 'nebulas';
  import VLayout from '@/layouts/NavLayout';
  import VCard from '@/components/Card'


  export default {
    components: {
      VLayout,
      VCard
    },
    /**
     * Chart data used to render stats, charts. Should be replaced with server data
     */
    data() {
      return {
        dappAddress: '',
        list: [],
        todos: [{'title':'one value','edit':false},
          {'title':'one value','edit':false},
          {'title':'otro titulo','edit':false}],
        editedTodo: null,
        message: 'Hello Vue.js!'
      };
    },
    created() {
      //从配置中读取智能合约DAPP地址
      this.dappAddress  = this.$neblib.dappAddress
      console.log('created dapp address', this.dappAddress);

      // 坚持星云钱包插件
      if (typeof (webExtensionWallet) === "undefined") {
        alert("请先安装星云钱包插件.");
      }

      this.onGet()

    },
    methods: {
      editTodo: function(todo) {
        console.log('edit todo');
        this.editedTodo = todo;
      },
      // 查询所有的
      onGet() {
        const neb_server_url = process.env.NEB_SERVER_URL;
        console.log('get from ', neb_server_url);

        const Account = nebulas.Account;
        let neb = new nebulas.Neb();
        neb.setRequest(new nebulas.HttpRequest(neb_server_url));
        const from = Account.NewAccount().getAddressString();

        const value = '0';
        const nonce = '0';
        const gas_price = '1000000';
        const gas_limit = '2000000';
        const callFunction = 'get';
        const callArgs = '["' + 'defsss' + '"]'; // in the form of ["args"]
        const contract = {
          function: callFunction,
          args: callArgs,
        };

        neb.api.call(from, this.dappAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
//          console.log('response of search:', JSON.stringify(resp));
          let result = resp.result;  // string
          console.log('result: ', result);
          result = JSON.parse(result) // obj
          if (!result) {
            console.log( ' no result');
            return
          }
          console.log('# list: ', result.list);
          this.list = result.list

        }).catch((err) => {
          console.log(`error:${err.message}`);
          alert(err.message)
        });
      },
      onPush(item) {
        console.log('jump url ', item.url);
        window.location.href=item.url;
      },

      pushNew() {
        console.log('push new');
        this.$router.push({name:'new'})
      }
    },

  };

</script>
<style>
  .row {
    margin-bottom: 20px;
    border-bottom: 1px solid #dddddd
  }
</style>



